<template>
  <div class="main">
    <div class="header">

      <div class="top" :style="style">

        <div class="banner-mask">
          <div class="Header">
            <Header></Header>
          </div>
          <div class="container">
            <div class="con">
              <h1 class="title">
                {{title}}
              </h1>
              <p>{{Introduction}}</p>
            </div>
          </div>
        </div>
      </div>

    </div>
    <div class="main-container">
        <div  class="list">
          <div>
           <Article></Article>
          </div>

        </div>

        <div class="footer">
              <Footer></Footer>
        </div>
    </div>
  </div>
</template>
<script>
import Header from '@/components/Header'
import Article from '@/components/Article/index.vue'
import Footer from '@/components/Footer/index.vue'
export default {
  name: 'Ho-me',
  data () {
    return {
      src: '',
      title: '',
      Introduction: '',
      content: ''
    }
  },
  components: {
    Header,
    Article,
    Footer
  },
  created () {
    this.src = this.$store.state.Content.src || sessionStorage.getItem('src')
    this.title = this.$store.state.Content.title || sessionStorage.getItem('title')
    this.Introduction = this.$store.state.Content.Introduction || sessionStorage.getItem('Introduction')
    document.documentElement.scrollTo({
      left: 0,
      top: 0,
      behavior: 'smooth'
    })
  },
  computed: {
    style () {
      return { 'background-image': `url(${this.src})` }
    }
  }
}
</script>
<style lang="less" scoped>
.main{
  background-color: #fff;
}
.banner-mask {
  width: 100%;
  height: 100%;
  background: rgba(30, 30, 30, 0.25);
  z-index: 1;
  color: #fff;
  .container {
    display: flex;
    justify-content: center;
    position: relative;
    .con {
      position: absolute;
      top: 30vh;
      left: 250px;

      .title {
        color: #fff;
        margin-bottom: 10px;
      }
    }
  }
  .Header {
    padding: 0px 100px;
    z-index: -1;
  }
}
.top {
  height: 100vh;
  background: url('https://www.bing.com/th?id=OHR.LacChesserys_ZH-CN4136691056_1920x1080.jpg&rf=LaDigue_1920x1080.jpg&pid=hp') no-repeat;
